import { env } from './../../environments/environment';
import { User } from './../layouts/login-layout/user.model';
import { map, tap } from 'rxjs/operators';
import { Observable, Subject } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Occupancy } from '../interfaces/occupancy';
import { stringify } from 'querystring';

interface AuthRespondeData {
  authToken: string;
}

@Injectable({
  providedIn: 'root'
})
export class FetchFromDBService {
  //questo url andrà poi nascosto
  login_api_url = env.protocol + env.host + ":"+  env.port + "/api/user/login";
  url = env.protocol + env.host + ":"+  env.port + "/posts";
  items = [];
  user = new Subject<User>();


  constructor(private http: HttpClient) 
    { }
    
    //map
    
    getConfig() {
      const headers = new HttpHeaders ({
        'Content-Type': 'application/json',
        'Authorization': 'Bearer '+localStorage.getItem('auth-token')
      })
      return this.http.get(this.url, { headers: headers });
    }
    Risposta : AuthRespondeData;
    login(username: string, password: string){
      return this.http.post<AuthRespondeData>(this.login_api_url,
      {
          email: username,
          password: password
      }
      ).pipe(tap (resData => {
        const user = new User(resData.authToken);
        this.user.next (user);
        localStorage.setItem('auth-token', resData.authToken);
      }
      ));
      }
    }
    /*getConfig() : Observable<Occupancy[]> {

          return this.http.get<Occupancy[]>(this.url);*/