import { Component, OnInit } from '@angular/core';


export interface RouteInfo {
    path: string;
    title: string;
    icon: string;
    class: string;
}

export const ROUTES: RouteInfo[] = [
    { path: '/dashboard',     title: 'Dashboard',           icon:'nc-bank',       class: '' },
    { path: '/icons',         title: 'Live',                icon:'nc-watch-time',    class: '' },
    { path: '/maps',          title: 'Analytics',           icon:'nc-chart-bar-32',      class: '' },
    { path: '/notifications', title: 'Reports',             icon:'nc-chart-pie-36',    class: '' },
    { path: '/user',          title: 'My places',           icon:'nc-layout-11',  class: '' },
    { path: '/table',         title: 'Settings',            icon:'nc-settings-gear-65',    class: '' },
    { path: '/typography',    title: 'Typography',          icon:'nc-caps-small', class: '' },
];

@Component({
    moduleId: module.id,
    selector: 'sidebar-cmp',
    templateUrl: 'sidebar.component.html',
})

export class SidebarComponent implements OnInit {
    public menuItems: any[];
    ngOnInit() {
        this.menuItems = ROUTES.filter(menuItem => menuItem);
    }
}
